<template>
  <div class="home">
    <el-container>
      <el-header>
        <el-form ref="form"
                 :model="form"
                 label-width="80px">
          <el-row>
            <h1></h1>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="">
                <el-date-picker v-model="form.date"
                                type="month"
                                value-format="yyyy-MM"
                                placeholder=""
                                @change="commonChange()"></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-select v-model="form.categories"
                           placeholder=""
                           multiple
                           collapse-tags
                           @change="commonChange()">
                  <el-option v-for="item in categoriesOptions"
                             :key="item.id"
                             :label="item.name"
                             :value="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-select v-model="form.sortType"
                           placeholder=""
                           @change="commonChange()"
                           clearable>
                  <el-option v-for="item in sortOptions"
                             :key="item.id"
                             :label="item.name"
                             :value="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="form.income"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="form.pay"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="form.count"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-header>
      <el-main class="mian-class">
        <el-row>
          <h1>
            
            <el-button icon="el-icon-edit"
                       type="warning"
                       circle
                       class="btn-style"
                       @click="add()"></el-button>
          </h1>
        </el-row>
        <el-row>
          <el-collapse v-model="activeName"
                       :data="table.data"
                       class="collapse-tyle">
            <el-collapse-item v-for="(item, idx) in table.data"
                              :key="idx"
                              :title="item.time.slice(0,7)+'/'+item.name+'/ '+item.amount"
                              :name="idx+'@'"
                              :class="itemColor(item.type)">
              <div>{{item.time}}</div>
              <div>id{{item.category}}</div>
              <div>{{item.amount}}</div>
              <div>{{item.type}}</div>
            </el-collapse-item>
          </el-collapse>
        </el-row>
      </el-main>

      <el-dialog title=""
                 :visible.sync="DialogVisible"
                 width="30%"
                 center>
        <addDialog ref="addBillRef"
                   :billDetail="billDetail"
                   :categoryDatail="categoryDatail"
                   :key="(new Date()).toString()"></addDialog>
        <div class="bt-foot">
          <el-button @click="doCancel()"> </el-button>
          <el-button type="primary"
                     @click="doEnter()"> </el-button>
        </div>
      </el-dialog>
    </el-container>
  </div>
</template>

<script>
import billCsv from "../views/csv/bill.csv";
import categoriesCsv from "../views/csv/categories.csv";
import { formatDate, formatType, currencyFormat } from "../utils/format.js";
import { clone, getParamType, compose } from "../utils/index.js";
import addDialog from "./addDialog";

export default {
  name: "Home",
  components: {
    addDialog
  },
  data() {
    return {
      tableData1: [], //
      tableData2: [], //
      activeName: [],
      cloneTable: [], //
      categoriesOptions: [], //tableData2,
      sortOptions: [
        { id: "", name: "" },
        { id: "", name: "" }
      ], //options
      billDetail: {
        //
        type: "",
        category: "",
        amount: "",
        time: null
      },
      categoryDatail: {
        //''
        id: "",
        type: "",
        name: ""
      },
      DialogVisible: false, //
      form: {
        //
        date: "",
        categories: [],
        sortType: "",
        income: 0,
        pay: 0,
        count: 0
      },
      table: {
        // 
        data: []
      }
    };
  },
  watch: {},

  computed: {},

  created() {
    this.init();
  },

  mounted() { },

  methods: {
    init() {
      // csv
      this.tableData1 = this.toJson(billCsv);
      this.tableData2 = this.toJson(categoriesCsv);

      //
      if (this.billDetail.time != null) {
        this.tableData1.push(this.billDetail);
        // 
        if (
          this.billDetail.category === "0000000000" &&
          !this.tableData2.find(v => v.id === "0000000000")
        ) {
          this.tableData2.push(this.categoryDatail);
        }
      }

      // 
      let t1 = clone(this.tableData1);
      let t2 = clone(this.tableData2);
      // 
      this.table.data = this.concatTable(t1, t2);
      this.formatHome(this.table.data, "type", "time", "amount");
      // 
      this.cloneTable = clone(this.table.data);
      // 
      this.calculate(this.table.data);

      //
      this.categoriesOptions = this.tableData2;
      sessionStorage.setItem(
        "categoriesOptions",
        JSON.stringify(this.tableData2)
      );
    },

    //csvkeyvalue,
    toJson(csv) {
      const arr = [];
      const keys = csv[0];
      const values = csv.slice(1);
      const len1 = values.length,
        len2 = keys.length;
      for (let i = 0; i < len1; i++) {
        for (let j = 0; j < len2; j++) {
          if (!arr[i]) {
            arr[i] = {};
          }
          arr[i][keys[j]] = values[i][j];
        }
      }
      return arr;
    },

    //
    concatTable(t1, t2) {
      let a = t1.map(v1 => {
        t2.forEach(v2 => {
          v1.category === v2.id ? Object.assign(v1, v2) : v1;
        });
        return v1;
      });
      return a;
    },

    //
    formatHome(data, k1, k2, k3) {
      data.forEach(v => {
        v[k1] = formatType(Number(v[k1]));
        v[k2] = formatDate(Number(v[k2]));
        v[k3] = currencyFormat(v[k3]);
      });
    },

    //num
    reAmount(s) {
      return parseFloat(s.replace(//, ""));
    },

    // 
    commonChange() {
      this.$nextTick(
        () => (this.table.data = this.allFliterCalculate(this.table.data))
      );
    },

    dateFliter(tableData) {
      if (this.form.date) {
        tableData = tableData.filter(
          v => v.time.slice(0, 7) === this.form.date
        );
      }
      return tableData;
    },

    categoriesFliter(tableData) {
      if (this.form.categories.length > 0) {
        tableData = tableData.filter(v =>
          this.form.categories.includes(v.category)
        );
      }
      return tableData;
    },

    sortFliter(tableData) {
      if (this.form.sortType === "") {
        tableData.sort(
          (a, b) => this.reAmount(a.amount) - this.reAmount(b.amount)
        );
      } else if (this.form.sortType === "") {
        tableData.sort(
          (a, b) => this.reAmount(b.amount) - this.reAmount(a.amount)
        );
      }
      return tableData;
    },

    allFliterCalculate(tableData) {
      tableData = this.cloneTable; //table.data
      return compose(
        this.sortFliter,
        this.calculate,
        this.categoriesFliter,
        this.dateFliter
      )(tableData);
    },

    //
    calculate(tableData) {
      this.form.income = 0;
      this.form.pay = 0; //
      if (tableData.length > 0) {
        tableData.forEach(v => {
          if (v.type === "") {
            this.form.income += this.reAmount(v.amount);
          } else {
            this.form.pay += this.reAmount(v.amount);
          }
        });
      }
      this.form.count = this.form.income - this.form.pay;
      return tableData;
    },

    itemColor(type) {
      if (type === "") {
        return "income-color";
      } else {
        return "pay-color";
      }
    },

    add() {
      this.DialogVisible = true;
    },

    doEnter() {
      return this.$refs.addBillRef.save().then(res => {
        if (res) {
          this.init();
          this.DialogVisible = false;
        }
      });
    },

    doCancel() {
      this.DialogVisible = false;
    }
  }
};
</script>

<style lang="less" scoped>
.home {
  .mian-class {
    margin-top: 90px;
  }
  .btn-style {
    position: relative;
    left: 35px;
  }
  .collapse-tyle {
    width: 40%;
    margin: 0 auto;
  }
  .income-color {
    /deep/.el-collapse-item__header {
      background-color: rgb(115, 222, 179);
    }
  }
  .pay-color {
    /deep/.el-collapse-item__header {
      background-color: rgb(235, 127, 101);
    }
  }
  .bt-foot {
    display: flex; //flex
    justify-content: center; //x
    align-items: center; //y
  }
}
</style>

