<template>
  <div class="addDialog">
    <el-form ref="formRef" label-width="100px" :model="form" :rules="rules">
      <el-row>
        <el-col :span="24">
          <el-form-item label="" prop="type">
            <el-select v-model="form.type" placeholder="">
              <el-option
                v-for="item in typeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="">
            <el-select v-model="form.category" placeholder="">
              <el-option
                v-for="item in categoriesOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="21">
          <el-form-item label="" prop="amount">
            <el-input v-model.trim="form.amount" placeholder=""></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import { formatDate, formatType, currencyFormat } from "../utils/format.js";
import { clone, getParamType, compose } from "../utils/index.js";

export default {
  name: "addDialog",
  props: {
    billDetail: {
      type: Object
    },
    categoryDatail: {
      type: Object
    }
  },
  data() {
    const amountValidate = (r, value, cb) => {
      let isTwoDecimal = /^\d*(\.)?(\d{0,2})?$/.test(value);
      if (!value) {
        cb(new Error(""));
      } else if ("" + Number(value) === "NaN") {
        cb(new Error(""));
      } else if (Number(value) && !isTwoDecimal) {
        cb(new Error("2"));
      } else {
        cb();
      }
    };
    return {
      typeOptions: [
        { id: "0", name: "" },
        { id: "1", name: "" }
      ],
      categoriesOptions: [],
      form: {
        type: "",
        category: "",
        amount: "",
        time: new Date().getTime()
      },
      rules: {
        type: {
          required: true,
          message: "",
          trigger: ["blur", "change"]
        },
        amount: {
          required: true,
          validator: amountValidate,
          trigger: ["blur", "change"]
        }
      }
    };
  },
  watch: {},

  computed: {},

  created() {
    this.init();
  },

  mounted() {},

  methods: {
    init() {
      this.categoriesOptions = JSON.parse(
        sessionStorage.getItem("categoriesOptions")
      );
    },

    /*  */
    validate() {
      return new Promise(resolve => {
        this.$refs.formRef.validate(res => {
          if (res) {
            resolve(this.form);
          } else {
            resolve(false);
          }
        });
      });
    },

    save() {
      return new Promise((resolve, reject) => {
        this.validate().then(res => {
          if (res) {
            if (!this.form.category) {
              this.form.category = "0000000000";
              let a = {
                id: "0000000000",
                type: this.form.type,
                name: ""
              };
              Object.assign(this.categoryDatail, a);
            }
            Object.assign(this.billDetail, this.form);
            resolve(true);
          } else {
            resolve(false);
          }
        });
      });
    }
  }
};
</script>

